<script>
import LayoutWebMobile from '@layouts/LayoutWebMobile'
import GxWizard from '@components/_CoreTaxes/Wizard/GxWizard'
import GxLoader from '@components/_Core/Utils/GxLoader'
import {
  CONFIG_LOAD_CHART_OF_ACCOUNTS_FROM_TEMPLATE,
  CONFIG_LOAD_CHART_OF_ACCOUNTS_FROM_FILE,
} from '@src/apollo/mutations'
import {
  GET_ACCOUNTING_FIRMS,
  GET_LEVELS_LEDGER_ACCOUNTS,
} from '@src/apollo/queries'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import GxUploadFile from '@components/_CoreTaxes/UploadFile/GxUploadFile'

export default {
  name: 'Templates',
  components: {
    LayoutWebMobile,
    GxWizard,
    GxCardModal,
    GxLoader,
    GxUploadFile,
  },
  data() {
    return {
      accountingFirm: null,
      clientNumber: null,
      loading: false,
      error: null,
      steps: [
        'Elige tu catálogo',
        'Personaliza tu catálogo',
        'Periodo inicial',
        'Saldos iniciales',
        '¡Listo!',
      ],
      errorDescription: '',
      levels: null,
      showUploadTemplate: false,
    }
  },
  apollo: {
    myAccountingFirms: {
      query: GET_ACCOUNTING_FIRMS,
    },
    getLevelsLedgerAccounts: {
      query: GET_LEVELS_LEDGER_ACCOUNTS,
      variables() {
        return {
          clientNumber: [this.clientNumber],
        }
      },
      update(data) {
        this.levels =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.levels
        if (this.levels > 0) {
          this.$router.push({
            name: 'previewTemplate',
          })
        }
      },
      fetchPolicy: 'no-cache',
    },
  },
  watch: {
    myAccountingFirms: {
      handler(newVal) {
        if (newVal) {
          this.accountingFirm = newVal[0]
          //console.log(newVal)
        }
      },
    },
  },
  created() {
    this.clientNumber = localStorage.getItem('companyNumber')
    //console.log(this.clientNumber)
  },
  methods: {
    goBack() {
      if (this.showUploadTemplate) {
        this.showUploadTemplate = false
      } else {
        this.$router.push({
          name: 'createNewAccountingClientView',
        })
      }
    },
    async goToPreviewTemplate(type) {
      if (type === 'template') {
        this.showUploadTemplate = true
      } else {
        try {
          const response = await this.$apollo.mutate({
            mutation: CONFIG_LOAD_CHART_OF_ACCOUNTS_FROM_TEMPLATE,
            variables: {
              accountingFirmNumber: this.accountingFirm.accountingFirmNumber,
              clientNumber: this.clientNumber,
              template: type,
            },
          })
          //console.log(response.data.configLoadChartOfAccountsFromTemplate)
          if (
            response.data.configLoadChartOfAccountsFromTemplate.error !==
              null &&
            response.data.configLoadChartOfAccountsFromTemplate.processID ===
              null
          ) {
            this.errorDescription =
              response.data.configLoadChartOfAccountsFromTemplate.error.message
            if (
              response.data.configLoadChartOfAccountsFromTemplate.error
                .details !== null
            )
              if (
                response.data.configLoadChartOfAccountsFromTemplate.error
                  .details.length > 0
              )
                this.errorDescription +=
                  ', ' +
                  response.data.configLoadChartOfAccountsFromTemplate.error.details.join(
                    ', '
                  )
            return
          } else {
            localStorage.setItem(
              'loadLedgerAccountProcessId',
              response.data.configLoadChartOfAccountsFromTemplate.processID
            )
            this.$router.push({
              name: 'previewTemplate',
              //params: { typeTemplateProps: type },
            })
          }
        } catch (error) {
          this.errorDescription = this.$utils.parseServerError(error)
          return null
        }
      }
      // this.$router.push({
      //   name: 'previewTemplate',
      //   params: { typeTemplateProps: type },
      // })
    },
    closeModel() {
      this.errorDescription = ''
    },
    async sendFile(fileBase64) {
      this.$root.$emit('setTemporaryFile64', fileBase64)
      try {
        const response = await this.$apollo.mutate({
          mutation: CONFIG_LOAD_CHART_OF_ACCOUNTS_FROM_FILE,
          variables: {
            accountingFirmNumber: this.accountingFirm.accountingFirmNumber,
            clientNumber: this.clientNumber,
            file: fileBase64,
          },
        })
        if (
          response.data.configLoadChartOfAccountsFromFile.error !== null &&
          response.data.configLoadChartOfAccountsFromFile.processID === null
        ) {
          this.errorDescription =
            response.data.configLoadChartOfAccountsFromFile.error.message
          if (
            response.data.configLoadChartOfAccountsFromFile.error.details !==
            null
          )
            if (
              response.data.configLoadChartOfAccountsFromFile.error.details
                .length > 0
            )
              this.errorDescription +=
                ', ' +
                response.data.configLoadChartOfAccountsFromFile.error.details.join(
                  ', '
                )
          return
        } else {
          localStorage.setItem(
            'loadLedgerAccountProcessId',
            response.data.configLoadChartOfAccountsFromFile.processID
          )
          this.$router.push({
            name: 'previewTemplate',
          })
        }
      } catch (error) {
        this.errorDescription = this.$utils.parseServerError(error)
        return null
      }
    },
  },
}
</script>
<template>
  <LayoutWebMobile icon="keyboard_backspace" :icon-click-handler="goBack">
    <div v-if="this.levels === null" class="flex flex-col w-full">
      <GxLoader class="mt-20 mb-5" />
    </div>
    <div v-else class="flex flex-col w-full items-center">
      <div class="flex flex-col text-xl mt-3"
        ><span class="font-black">Configuración Inicial</span></div
      >

      <div class="flex flex-col mt-10">
        <GxWizard :stepsProp="steps" :stepActiveProp="1" />
      </div>
      <div class="flex flex-col text-base mt-12"
        ><span class="font-black"
          >Selecciona el catálogo base que quieres utilizar</span
        ></div
      >

      <div
        v-if="!this.showUploadTemplate"
        class="flex flex-col items-stretch w-159 mt-5"
      >
        <div class="flex ml-5">
          <div
            class="group flex flex-col w-69 border h-24 parent rounded-lg items-center m-2 hover:border-primary hover:shadow-12 cursor-pointer"
            @click="goToPreviewTemplate('mexProducts')"
            ><div
              class="name mt-4 text-base font-bold group-hover:text-primary"
            >
              Comercial
            </div>
            <div class="mt-2 ml-3 mr-3 text-center font-bold text-gray-600">
              Cuenta con sección de inventario y costo de ventas
            </div>
          </div>

          <div
            class="group flex flex-col w-69  border h-24 rounded-lg items-center m-2 hover:border-primary hover:shadow-12 cursor-pointer"
            @click="goToPreviewTemplate('mexServices')"
            ><div class="mt-4 text-base font-bold group-hover:text-primary">
              Servicios
            </div>
            <div class="mt-2 ml-3 mr-3 text-center font-bold text-gray-600">
              Catálogo sugerido para negocios <br />
              de servicios
            </div>
          </div>
        </div>
        <div class="flex x ml-5">
          <div
            class="group flex flex-col w-69  border h-24 rounded-lg items-center m-2 hover:border-primary hover:shadow-12 cursor-pointer"
            @click="goToPreviewTemplate('mexSat')"
            ><div class="mt-4 text-base font-bold group-hover:text-primary">
              SAT
            </div>
            <div class="mt-2 ml-3 mr-3 text-center font-bold text-gray-600">
              Catálogo estructurado con base <br />en el catálogo del SAT
            </div>
          </div>

          <div
            class="group flex flex-col w-69  border h-24 rounded-lg items-center m-2 hover:border-primary hover:shadow-12 cursor-pointer"
            @click="goToPreviewTemplate('template')"
            ><div class="mt-4 text-base font-bold group-hover:text-primary">
              Mi propio catálogo
            </div>
            <div class="mt-2 ml-3 mr-3 text-center font-bold text-gray-600">
              Cargar mi propio catálogo <br />
              mediante una plantilla de excel
            </div>
          </div>
        </div>
      </div>

      <div v-else class="mt-5">
        <GxUploadFile
          class="w-100"
          v-bind:style="{ width: '550px' }"
          ref="gxUploadFile"
          :fileType="'respaldo'"
          fileDownload="https://gxyaksgtest.blob.core.windows.net/templates/Plantilla_de_Importacion_de_Cuentas_Contables.xlsx"
          :extensions="Array('.xlsx', '.xls')"
          @sendFile="sendFile"
        />
      </div>
    </div>

    <div
      class="flex flex-col fixed w-full bottom-0 items-start border-t h-19 shadow-8"
    >
      <GxButton
        :variant="true"
        :ghost="true"
        class="ml-10 mt-4 bottom-0 rounded-md gx-button"
        :loading="loading"
        @click="goBack"
      >
        CANCELAR
      </GxButton>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Error al tratar de crear el catálogo de cuentas'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </LayoutWebMobile>
</template>
